<template>
  <v-container fill-height>
    <v-row
      align="center"
      justify="center"
    >
      <v-col cols="6">

        <v-carousel
          :show-arrows="true"
          show-arrows-on-hover
          height="350"
          :hide-delimiters="true"
        >
          <v-carousel-item
            v-for="(pet,i) in petitions"
            :key="i"
            reverse-transition="fade-transition"
            transition="fade-transition"
          >
            <v-layout
              column
              align-content-center
            >

              <v-flex xs6>
                <v-card>
                  <v-card-title
                    primary-title
                    mb-5
                  >
                    Top 100
                  </v-card-title>

                  <my-petition
                    :petition="pet"
                    :color="color()"
                    signable
                  >
                  </my-petition>

                </v-card>

              </v-flex>

            </v-layout>

          </v-carousel-item>
        </v-carousel>
      </v-col>

    </v-row>

  </v-container>

</template>

<script>
import axios from 'axios'
import Petition from '@/components/Petition.vue'
export default {
  components: {
    'my-petition': Petition
  },
  mounted () {
    axios.get('top100').then(response => {
      this.petitions = response.data.items.map(pet => pet.properties)
    }
    )
  },
  data: () => ({
    petitions: []
  }),
  methods: {
    color () {
      return randomColor()
    }
  }
}
</script>
