<template>
  <v-card
    :color="color"
    dark
    hover
    width="100%"
    mb-3
  >
    <v-alert
      dismissible
      type="success"
      :value="success"
    >
      {{successMsg}}

    </v-alert>
    <v-alert
      dismissible
      type="error"
      :value="error"
    >

    </v-alert>
    <v-card-text class="white--text">
      <div class="headline mb-2">{{petition.name}}</div>
      {{petition.description}}
    </v-card-text>
    <v-list-item class="grow">
      <v-list-item-content>
        <v-list-item-title>{{petition.owner}}</v-list-item-title>
      </v-list-item-content>

      <v-row
        align="center"
        justify="end"
      >
        <v-icon class="mr-1">mdi-heart</v-icon>
        <span class="subheading mr-2">{{petition.total}}</span>
      </v-row>
      <v-row
        v-if="signable"
        align="center"
        justify="center"
      >
        <v-btn
          icon
          dark
          @click="sign"
        >
          <v-icon>mdi-marker</v-icon>
        </v-btn>
      </v-row>
    </v-list-item>

  </v-card>
</template>
<script>
import axios from 'axios'
import { mapGetters } from 'vuex'
export default {
  props: {
    petition: Object,
    color: String,
    signable: Boolean
  },
  data: () => ({
    success: false,
    error: false,
    successMsg: '',
    errorMsg: ''

  }),
  methods: {
    sign () {
      axios.post(`sign/?access_token=${this.token}`, {
        petitionName: this.petition.name, userName: this.user.email
      })
        .then(response => {
          this.petition = response.data.properties
          this.successMsg = 'Petition ajoutée ! Merci pour votre contribution'
          this.success = true
        })
        .catch(err => {
          this.errorMsg = err.response
          this.error = true
        })
    }
  },
  computed: {
    ...mapGetters({
      user: 'getUser',
      token: 'getToken'
    })
  }

}
</script>
