import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: null,
    token: null
  },
  getters: {
    getUser: state => {
      return state.user
    },
    getToken: state => {
      return state.token
    }

  },
  mutations: {
    setUser (state, payload) {
      this.state.user = payload
    },
    setToken (state, payload) {
      this.state.token = payload
    }

  },
  actions: {
    assignUser ({ commit }, payload) {
      commit('setUser', payload)
    },
    assignToken ({ commit }, payload) {
      commit('setToken', payload)
    }

  }
})
