<template>
  <v-app>
    <v-app-bar
      app
      dense
      floating
      clipped-left
    >
      <v-app-bar-nav-icon to="/"></v-app-bar-nav-icon>

      <v-toolbar-title>Welcome to TinyPet'</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn
        v-if="!user"
        icon
        @click="login"
      >
        <v-icon>mdi-login</v-icon>
      </v-btn>

      <v-btn
        v-else
        icon
        @click="logout"
      >
        <v-icon>mdi-logout</v-icon>
      </v-btn>

    </v-app-bar>
    <v-navigation-drawer
      expand-on-hover
      app
      v-if="user"
      clipped
    >
      <template v-slot:prepend>
        <v-list>
          <v-list-item>
            <v-list-item-avatar>
              <v-img :src="user.image"></v-img>
            </v-list-item-avatar>
          </v-list-item>

          <v-list-item two-line>
            <v-list-item-content>
              <v-list-item-title class="title">{{user.fullName}}</v-list-item-title>
              <v-list-item-subtitle>{{user.email}}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </template>

      <v-divider></v-divider>

      <v-list
        nav
        dense
      >
        <v-list-item
          link
          to="/newPetition"
        >
          <v-list-item-icon>
            <v-icon>mdi-file-plus</v-icon>
          </v-list-item-icon>
          <v-list-item-title>Ajouter une nouvelle pétition</v-list-item-title>
        </v-list-item>
        <v-list-item
          link
          to="/myPetitions"
        >
          <v-list-item-icon>
            <v-icon>mdi-file</v-icon>
          </v-list-item-icon>
          <v-list-item-title>Mes pétitions</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-content>
      <router-view></router-view>
    </v-content>
    <v-footer
      dark
      padless
    >
      <v-card
        flat
        tile
        class="indigo lighten-1 white--text text-center"
        width="100%"
      >
        <v-card-text>
          <v-btn
            v-for="icon in icons"
            :key="icon"
            class="mx-4 white--text"
            icon
          >
            <v-icon size="24px">{{ icon }}</v-icon>
          </v-btn>
        </v-card-text>

        <v-card-text
          class="white--text"
          pr-5
        >
          Projet de développement application cloud native utilisant GAE et Vue.js réalisé pendant mon M1 Miage

        </v-card-text>

        <v-divider></v-divider>

        <v-card-text class="white--text">
          {{ new Date().getFullYear() }} — <strong>MG</strong>
        </v-card-text>
      </v-card>
    </v-footer>
  </v-app>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'App',
  data: () => ({
    icons: [
      'mdi-facebook',
      'mdi-twitter',
      'mdi-google-plus',
      'mdi-linkedin',
      'mdi-instagram'
    ]
  }),
  methods: {
    ...mapActions([
      'assignUser',
      'assignToken'
    ]),
    login () {
      gapi.auth2.getAuthInstance().signIn()
        .then(() => {
          const googleUser = gapi.auth2.getAuthInstance().currentUser.get()
          const profile = googleUser.getBasicProfile()
          const user = {
            id: profile.getId(),
            fullName: profile.getName(),
            givenName: profile.getGivenName(),
            familyName: profile.getFamilyName(),
            image: profile.getImageUrl(),
            email: profile.getEmail()
          }

          this.assignUser(user)
          this.assignToken(googleUser.getAuthResponse().id_token)
        })
        .catch(err => console.log(err))
    },
    logout () {
      this.assignToken(null)
      this.assignUser(null)
      this.$router.push('/')
    }
  },
  computed: {
    ...mapGetters({
      user: 'getUser'
    })
  }
}
</script>
