<template>
  <v-container fill-height>
    <v-row
      align="center"
      justify="center"
    >
      <v-col cols="6">

        <v-carousel
          :show-arrows="false"
          height="300"
        >
          <v-carousel-item
            v-for="(pet,i) in petitions"
            :key="i"
            reverse-transition="fade-transition"
            transition="fade-transition"
          >
            <v-layout
              column
              align-content-center
            >

              <v-flex xs6>
                <v-card>
                  <v-card-title
                    primary-title
                    mb-5
                  >
                    Petitions à la une
                  </v-card-title>

                  <my-petition
                    :petition="pet"
                    :color="color()"
                    signable
                  >
                  </my-petition>
                  <v-card-actions>
                    <v-layout
                      row
                      wrap
                      align-center
                      justify-center
                    >
                      <v-btn
                        icon
                        @click="refresh"
                      >
                        <v-icon>mdi-refresh</v-icon>
                      </v-btn>

                    </v-layout>

                  </v-card-actions>

                </v-card>

              </v-flex>

            </v-layout>

          </v-carousel-item>
        </v-carousel>
      </v-col>

    </v-row>

  </v-container>

</template>

<script>
import axios from 'axios'
import Petition from '@/components/Petition.vue'
export default {
  components: {
    'my-petition': Petition
  },
  mounted () {
    axios.post('all/').then(response => {
      this.petitions = response.data.petitions.map(pet => pet.properties)
    }
    )
  },
  data: () => ({
    petitions: []
  }),
  methods: {
    color () {
      return randomColor()
    },
    refresh () {
      const key = this.$_.last(this.petitions).name
      axios.post('all/', {
        lastKey: key
      }).then(response => {
        this.petitions = response.data.petitions.map(pet => pet.properties)
      }
      )
    }
  }
}
</script>
