import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import MyPetitions from './views/MyPetitions.vue'
import NewPetitions from './components/PetitionAdd.vue'
import store from './store.js'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/about',
      name: 'about'
    },
    {
      path: '/myPetitions',
      name: 'myPetitions',
      component: MyPetitions,
      beforeEnter (to, from, next) {
        if (store.state.user) {
          next()
        } else {
          next('/')
        }
      }
    },
    {
      path: '/newPetition',
      name: 'newPetition',
      component: NewPetitions,
      beforeEnter (to, from, next) {
        if (store.state.user) {
          next()
        } else {
          next('/')
        }
      }
    }
  ]
})
