<template>
  <v-container>
    <v-alert
      dismissible
      type="success"
      :value="success"
    >
      {{successMsg}}

    </v-alert>
    <v-alert
      dismissible
      type="error"
      :value="error"
    >

    </v-alert>

    <v-card
      class="mx-auto"
      style="max-width: 600px;"
    >
      <v-system-bar
        color="deep-purple darken-4"
        dark
      >
        <v-spacer></v-spacer>
        <v-icon small>mdi-square</v-icon>
        <v-icon
          class="ml-1"
          small
        >mdi-circle</v-icon>
        <v-icon
          class="ml-1"
          small
        >mdi-triangle</v-icon>
      </v-system-bar>
      <v-toolbar
        color="deep-purple accent-4"
        cards
        dark
        flat
      >
        <v-card-title class="title font-weight-regular">Renseigner les informations suivantes pour votre nouvelle pétition</v-card-title>
        <v-spacer></v-spacer>
      </v-toolbar>
      <v-form
        ref="form"
        v-model="form"
        class="pa-4 pt-6"
      >
        <v-text-field
          v-model="petition.name"
          :rules="[required]"
          filled
          color="deep-purple"
          label="Titre de la pétition"
        ></v-text-field>
        <v-textarea
          v-model="petition.description"
          auto-grow
          filled
          :rules="[required]"
          color="deep-purple"
          label="Description"
          rows="2"
        ></v-textarea>
        <v-checkbox
          v-model="agreement"
          :rules="[required]"
          color="deep-purple"
        >
          <template v-slot:label>
            J'accepte les&nbsp;
            <a
              href="#"
              @click.stop.prevent="dialog = true"
            >conditions d'utilisation</a>
            &nbsp;et&nbsp;
            <a
              href="#"
              @click.stop.prevent="dialog = true"
            >du traitement de mes données</a>*
          </template>
        </v-checkbox>
      </v-form>
      <v-divider></v-divider>
      <v-card-actions>
        <v-btn
          text
          @click="$refs.form.reset()"
        >
          Effacer
        </v-btn>
        <v-spacer></v-spacer>
        <v-btn
          :disabled="!form"
          :loading="isLoading"
          class="white--text"
          color="deep-purple accent-4"
          depressed
          @click="submit"
        >Valider</v-btn>
      </v-card-actions>
      <v-dialog
        v-model="dialog"
        absolute
        max-width="400"
        persistent
      >
        <v-card>
          <v-card-title class="headline grey lighten-3">Informations légale</v-card-title>
          <v-card-text>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions>
            <v-btn
              text
              @click="agreement = false, dialog = false"
            >
              No
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn
              class="white--text"
              color="deep-purple accent-4"
              @click="agreement = true, dialog = false"
            >
              Yes
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-card>

  </v-container>
</template>
<script>
import { mapGetters } from 'vuex'
import axios from 'axios'
export default {
  data () {
    return {
      success: false,
      error: false,
      successMsg: '',
      errorMsg: '',
      required: v => !!v || 'ce champs est obligatoire',
      petition: {
        name: '',
        description: ''
      },
      form: false,
      agreement: false,
      isLoading: false,
      dialog: false
    }
  },
  methods: {
    submit () {
      if (this.$refs.form.validate()) {
        this.petition.owner = this.user.email
        axios.post(`add/?access_token=${this.token}`, this.petition)
          .then(response => {
            this.successMsg = 'Pétition ajoutée !'
            this.success = true
          }
          )
          .catch(err => {
            this.errorMsg = err.response
            this.error = true
          })
      }
    }
  },
  computed: {
    ...mapGetters({
      user: 'getUser',
      token: 'getToken'
    })
  }

}
</script>
