<template>
  <v-container>
    <v-layout
      row
      wrap
      justify-center
    >
      <v-data-iterator
        :items="myPetitions"
        :items-per-page.sync="itemsPerPage"
        :footer-props="{ itemsPerPageOptions }"
      >
        <template v-slot:header>
          <v-toolbar
            class="mb-2"
            color="indigo darken-5"
            dark
            flat
          >
            <v-toolbar-title>Mes pétitions signées et crées</v-toolbar-title>
          </v-toolbar>
        </template>
        <template v-slot:default="props">

          <v-layout
            v-for="item in props.items"
            mb-2
            :key="item.name"
          >
            <my-petition
              :petition="item"
              :color="color()"
            >
            </my-petition>
          </v-layout>
        </template>
      </v-data-iterator>

    </v-layout>

  </v-container>
</template>

<script>
import Petition from '@/components/Petition.vue'
import axios from 'axios'
import { mapGetters } from 'vuex'
export default {
  components: {
    'my-petition': Petition
  },
  mounted () {
    axios.post('ListByUserName/', {
      userName: this.user.email
    })
      .then(response => {
        this.myPetitions = response.data.items.map(pet => pet.properties)
      })
  },
  data () {
    return {
      itemsPerPageOptions: [2, 4, 8, 12],
      itemsPerPage: 2,
      myPetitions: []

    }
  },
  methods: {
    color () {
      return randomColor()
    }
  },
  computed: {
    ...mapGetters({
      user: 'getUser'
    })
  }
}
</script>
